t: 90px;\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n}\r\n\r\n.navbar .logo a {\r\n  font-size: 2rem;\r\n  font-weight: bold;\r\n  color: orange;\r\n}\r\n\r\n.navbar .logo a:hover {\r\n  color: #c13f17;\r\n}\r\n\r\n.navbar .links {\r\n  display: flex;\r\n  gap: 2rem;\r\n}\r\n\r\n.links li a {\r\n  font-size: 24px;\r\n}\r\n\r\n.navbar .toggle_btn {\r\n  color: black;\r\n  font-size: 1.5rem;\r\n  cursor: pointer;\r\n  display: none;\r\n}\r\n\r\n.toggle_btn {\r\n  background: transparent;\r\n  border: none;\r\n}\r\n\r\n.action_btn {\r\n  background-color: orange;\r\n  color: #fff;\r\n  padding: 16px 32px;\r\n  border: none;\r\n  outline: none;\r\n  border-radius: 20px;\r\n  font-size: 1.1rem;\r\n  font-weight: bold;\r\n  cursor: pointer;\r\n  transition: scale 0.2 ease;\r\n}\r\n\r\n.action_btn:hover {\r\n  scale: 1.05;\r\n  color: #c13f17;\r\n}\r\n\r\n.action_btn:active {\r\n  scale: 0.95;\r\n}\r\n\r\n.dropdown_menu {\r\n  display: none;\r\n  position: absolute;\r\n  right: 2rem;\r\n  top: 60px;\r\n  height: 0;\r\n  width: 300px;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  backdrop-filter: blur(15px);\r\n  border-radius: 10px;\r\n  overflow: hidden;\r\n  transition: height 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);\r\n}\r\n\r\n.dropdown_menu.open {\r\n  height: 300px;\r\n}\r\n\r\n.dropdown_menu.open ul li a {\r\n  color: #993a1f;\r\n  font-weight: 500;\r\n}\r\n\r\n.dropdown_menu li {\r\n  padding: 0.7rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n.dropdown_menu .action_btn {\r\n  width: 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n/* HERO */\r\n.hero {\r\n  height: 100vh;\r\n  background-color: #000;\r\n  background-image: url('images/inner-hero-small.jpg');\r\n  background-size: cover;\r\n  background-position: center;\r\n  height: calc(100vh - 60px);\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  text-align: center;\r\n  color: #fff;\r\n}\r\n\r\n#hero h1 {\r\n  font-size: 3rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n#hero p {\r\n  font-size: 1.5rem;\r\n  padding: 2px;\r\n}\r\n\r\n/* CONTENT */\r\nmain {\r\n  /* padding: 32px; */\r\n  flex: 1;\r\n}\r\n\r\n.information {\r\n  padding: 32px;\r\n  margin: 0 auto;\r\n  /* min-height: 100%; */\r\n}\r\n\r\n.information .content__heading {\r\n  color: #db6816;\r\n  text-align: center;\r\n}\r\n\r\n/*\r\n    LIST RESTORANTS\r\n  */\r\n\r\n.restaurants {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n\r\n/*\r\n    restaurant Item\r\n  */\r\n\r\n.restaurant-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant-item__header {\r\n  position: relative;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4;\r\n  /* number of lines to show */\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/*\r\n    DETAIL RESTORAN\r\n  */\r\n\r\n.resto {\r\n  padding: 32px;\r\n  